import React, { useEffect, useState } from "react";
import Loader from "../../../common/loader/Loader";
import { Select as AntSelect, message } from "antd";
import {
  getRetailersForASMZSM,
  getfundTransferExcelReportforASMASM,
  getfundTransferforASMASM,
} from "../../../api/login/Login";
import {
  Pagination,
  Autocomplete,
  Box,
  TextField,
  Typography,
  FormControl,
  InputLabel,
  MenuItem,
  Select,
} from "@mui/material";
import { Button } from "react-bootstrap";
import { Link } from "react-router-dom";
import * as XLSX from "xlsx";
import { useReportDates } from "../../../utils/getDate";

const AsmFundTransferReport = ({ title }) => {
  const today = new Date().toISOString().split("T")[0];
  const [loading, setLoading] = useState(false);
  const [selectedRow, setSelectedRow] = useState(null);
  const [page, setPage] = useState(1);
  const [retailers, setRetailers] = useState([]);
  const [selectedRetailer, setSelectedRetailer] = useState(null);
  const [totalCount, setTotalCount] = useState(0);
  const [submit, setSubmit] = useState(false);
  const [data, setData] = useState([]);

  const [rowsPerPage, setRowsPerPage] = useState(10);

  const [filter, setFilter] = useState({
    start_date: "",
    end_date: "",
    trans_type: "",
    user_mobile: "",
    user_type: "",
  });

  const userTypeOptions = [
    { value: "", label: "All" },
    { value: "Distributor", label: "Distributor" },
    { value: "Super Distributor", label: "Super Distributor" },
    { value: "Retailer", label: "Retailer" },
    { value: "asm", label: "ASM" },
    { value: "zsm", label: "ZSM" },
  ];

  const transTypeOptions = [
    { value: "", label: "All" },
    { value: "balance Reverse", label: "Balance Reverse" },
    { value: "balance transfer", label: "Balance Transfer" },
  ];

  const getRetailers = async () => {
    try {
      let res = await getRetailersForASMZSM();
      if (res?.data?.error === false) {
        setRetailers(res.data?.data || []);
      } else {
        setRetailers([]);
      }
    } catch (err) {
      console.error(err);
      setRetailers([]);
    }
  };

  const handleDateChange = (e) => {
    const { name, value } = e.target;

    setFilter((prev) => {
      // Start date changed
      if (name === "start_date") {
        return {
          ...prev,
          start_date: value,
          // clear end date if it becomes invalid
          end_date: prev.end_date && prev.end_date < value ? "" : prev.end_date,
        };
      }

      // End date validation
      if (name === "end_date") {
        if (prev.start_date && value < prev.start_date) {
          message.error("End date cannot be earlier than Start date");
          return prev; // â›” do NOT auto-set anything
        }
        return {
          ...prev,
          end_date: value,
        };
      }

      return {
        ...prev,
        [name]: value,
      };
    });
  };

  const handleStatusChange = (value, key) => {
    setFilter((prev) => ({
      ...prev,
      [key]: value,
    }));
  };

  const handleSearch = () => {
    setPage(1);
    setSubmit((prev) => !prev);
  };

  const ResetData = () => {
    setFilter({
      start_date: "",
      end_date: "",
      trans_type: "",
      user_mobile: "",
      user_type: "",
    });
    setSelectedRetailer(null);
    setRowsPerPage(10);
    setPage(1);
    setSubmit((prev) => !prev);
  };

  const handleChangeRowsPerPage = (e) => {
    setRowsPerPage(e.target.value);
    setPage(1);
  };

  const handleChangePage = (event, value) => {
    setPage(value);
  };

  //   const handleViewClick = (item) => {
  //     setSelectedRow(item);
  //   };

  useEffect(() => {
    getRetailers();
  }, []);

  const getData = async () => {
    setLoading(true);
    try {
      const res = await getfundTransferforASMASM({
        ...filter,
        retailer_id: selectedRetailer?._id || "",
        page,
        count: rowsPerPage,
      });
      if (res?.data?.error === false) {
        setData(res.data?.data || []);
        setTotalCount(res.data?.totalCount || 0);
      } else {
        setData([]);
        setTotalCount(0);
        message.error(res.data?.message || "Something went wrong");
      }
    } catch (error) {
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    getData();
  }, [submit, page, rowsPerPage]);

  //   const responseEnquiry = async (id) => {
  //     setLoading(true);
  //     try {
  //       let res = await enquirybbpsforASMASM(id);
  //       if (res?.data?.error === false) {
  //         message.success(res.data?.message);
  //         setSubmit((prev) => !prev);
  //       } else {
  //         message.error(res.data?.message);
  //       }
  //     } catch (error) {
  //     } finally {
  //       setLoading(false);
  //     }
  //   };

  const downloadExcel = async () => {
    try {
      setLoading(true);
      const res = await getfundTransferExcelReportforASMASM({
        ...filter,
        retailer_id: selectedRetailer?._id || "",
      });

      if (res?.data?.data?.length > 0) {
        const dataForExcel = res?.data?.data.map((item, i) => ({
          "Sr. No": i + 1,
          "Date / Time": item?.createdAt || "",
          "User Type": item?.user_type || "",
          "User Mobile Number": item?.user_mobile || "",
          "Transaction ID": item?.txn_id || "",
          Amount: item?.amount || "",
          "Opening Balance": item?.opening_balance || "",
          "Closing Balance": item?.closing_balance || "",
          "Transaction Type": item?.trans_type || "",
          "Retailer Name": item?.user_name || "",
          "Refer ID": item?.user_refer_id || "",
        }));

        const ws = XLSX.utils.json_to_sheet(dataForExcel);
        const wb = XLSX.utils.book_new();
        XLSX.utils.book_append_sheet(wb, ws, "Fund_Transfer_Report");
        XLSX.writeFile(
          wb,
          `Fund_Transfer_Report_${new Date().toLocaleDateString()}.xlsx`,
        );
      } else {
        message.error("No data available to export!");
      }
    } catch (error) {
      message.error("Failed to download Excel. Please try again.");
    } finally {
      setLoading(false);
    }
  };

  const totalPages = Math.ceil(totalCount / rowsPerPage);

  return (
    <>
      {loading && <Loader />}

      {title && (
        <h3
          className="mt-3 mb-3"
          style={{
            color: "var(--bs-primary2)",
            fontWeight: 700,
            borderBottom: "1px solid var(--bs-primary2)",
            paddingBottom: "8px",
          }}
        >
          {title}
        </h3>
      )}
      <div className="card">
        <div className="card-body">
          <form>
            <div className="form-row" style={{ alignItems: "end" }}>
              <div className="form-group col-md-4">
                <label>Start Date</label>
                <input
                  type="date"
                  name="start_date"
                  className="form-control input-white-bg"
                  max={today}
                  value={filter.start_date}
                  onChange={handleDateChange}
                />
              </div>
              <div className="form-group col-md-4">
                <label>End Date</label>
                <input
                  type="date"
                  name="end_date"
                  className="form-control input-white-bg"
                  max={today}
                  min={filter.start_date || ""}
                  value={filter.end_date}
                  onChange={handleDateChange}
                />
              </div>
              <div className="form-group col-md-4">
                <label>User Type</label>
                <AntSelect
                  style={{ width: "100%" }}
                  value={filter.user_type}
                  onChange={(value) => handleStatusChange(value, "user_type")}
                  options={userTypeOptions}
                />
              </div>

              <div className="form-group col-md-4">
                <label>Transaction Type</label>
                <AntSelect
                  style={{ width: "100%" }}
                  value={filter.trans_type}
                  onChange={(value) => handleStatusChange(value, "trans_type")}
                  options={transTypeOptions}
                />
              </div>
              <div className="form-group col-md-4">
                <label>Mobile Number</label>
                <input
                  type="text"
                  name="user_mobile"
                  className="form-control bs-autofill-fix"
                  placeholder="Enter Mobile Number"
                  value={filter.user_mobile}
                  maxLength={10}
                  onChange={(e) => {
                    const value = e.target.value.replace(/\D/g, "");

                    setFilter((prev) => ({
                      ...prev,
                      user_mobile: value,
                    }));
                  }}
                  onWheel={(e) => e.target.blur()}
                />
              </div>

              <div className="form-group col-md-4">
                <label>Select Retailer</label>
                <Autocomplete
                  options={retailers}
                  getOptionLabel={(option) => option.name || ""}
                  value={selectedRetailer ?? null}
                  size="small"
                  onChange={(e, newVal) => setSelectedRetailer(newVal)}
                  renderOption={(props, option) => (
                    <li {...props} key={option.retailer_id}>
                      <Box sx={{ fontSize: "13px" }}>{option.name}</Box>
                    </li>
                  )}
                  renderInput={(params) => (
                    <TextField
                      {...params}
                      label="Select Retailer"
                      InputLabelProps={{
                        sx: {
                          "&.MuiInputLabel-root:not(.MuiInputLabel-shrink)": {
                            top: "-3px",
                          },
                        },
                      }}
                      sx={{
                        "& label.Mui-focused": {
                          color: "#8b8b8bff",
                        },
                        "& .MuiInputBase-root": { backgroundColor: "#fff" },
                        "& .MuiOutlinedInput-root": {
                          "&:hover fieldset": {
                            borderColor: "#ced4da",
                          },
                          "&.Mui-focused fieldset": {
                            borderColor: "#ced4da",
                          },
                        },
                      }}
                    />
                  )}
                />
              </div>
              <div className="form-group col-md-12">
                <button
                  type="button"
                  className="btn mr-3 text-white"
                  style={{ backgroundColor: "#6C757D" }}
                  onClick={handleSearch}
                >
                  Search
                </button>
                <button
                  type="button"
                  className="btn btn-light"
                  style={{ backgroundColor: "#F8F9FA" }}
                  onClick={ResetData}
                >
                  Reset
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
      <div className="card mt-4" style={{ overflow: "auto" }}>
        <div className="card-body">
          <div id="myTable_wrapper" className="dataTables_wrapper no-footer">
            <div className="d-flex  justify-content-end align-items-center">
              <div className="dataTables_length mb-3" id="myTable_length">
                <Button
                  className="btn btn-success cusxel"
                  onClick={downloadExcel}
                  // disabled={loading}
                >
                  Download Excel sheet
                </Button>
              </div>
            </div>

            <div
              className="table-responsive"
              style={{
                maxHeight: "60vh",
                overflow: "auto",
                borderRadius: "10px",
                border: "1px solid #eaeaea",
              }}
            >
              <table
                className="table table-hover table-bordered mb-0"
                style={{ whiteSpace: "nowrap" }}
              >
                <thead>
                  <tr>
                    <th>S.No</th>
                    <th>Txn Date</th>
                    <th>Txn Id</th>
                    <th>Transaction Type</th>
                    <th>User Type</th>
                    <th>User Mobile Number</th>
                    <th>Opening Balance</th>
                    <th>Amount</th>
                    <th>Closing Balance</th>
                    <th>Retailer Name</th>
                    <th>Refer Code</th>
                  </tr>
                </thead>
                <tbody>
                  {data?.map((item, i) => (
                    <tr key={item?._id}>
                      <td>{i + 1 + (page - 1) * rowsPerPage}</td>
                      <td>{item?.createdAt}</td>
                      <td>{item?.txn_id || "---"}</td>
                      <td>{item?.trans_type}</td>
                      <td>{item?.user_type}</td>
                      <td>{item?.user_mobile}</td>

                      <td>{item?.opening_balance}</td>
                      <td>{item?.amount}</td>
                      <td>{item?.closing_balance}</td>
                      <td>{item?.user_name}</td>
                      <td>{item?.user_refer_id || ""}</td>
                    </tr>
                  ))}

                  {data?.length === 0 && (
                    <tr>
                      <td colSpan={11} style={{ textAlign: "center" }}>
                        No Record Found
                      </td>
                    </tr>
                  )}
                </tbody>
              </table>
            </div>
          </div>
          {/* </div> */}
          <Box
            display="flex"
            justifyContent="space-between"
            alignItems="center"
            mt={2}
            flexWrap="wrap"
            gap={2}
          >
            <Box display="flex" alignItems="center" gap={2}>
              <FormControl size="small" sx={{ width: "150px" }}>
                <InputLabel>Rows per page</InputLabel>
                <Select
                  value={rowsPerPage}
                  onChange={handleChangeRowsPerPage}
                  label="Rows per page"
                  sx={{
                    fontSize: "14px",
                    "& .MuiOutlinedInput-notchedOutline": {
                      borderColor: "#ccc",
                    },
                    "&:hover .MuiOutlinedInput-notchedOutline": {
                      borderColor: "var(--bs-primary1)",
                    },
                    "&.Mui-focused .MuiOutlinedInput-notchedOutline": {
                      borderColor: "var(--bs-primary1)",
                    },
                  }}
                >
                  {[5, 10, 25, 50].map((num) => (
                    <MenuItem key={num} value={num}>
                      {num}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
              <Typography
                variant="body2"
                sx={{ fontSize: "14px", color: "#555" }}
              >
                Total Entries: {totalCount}
              </Typography>
            </Box>
            <Pagination
              count={totalPages}
              page={page}
              onChange={handleChangePage}
              shape="rounded"
              showFirstButton
              showLastButton
              sx={{
                "& .MuiPaginationItem-root": {
                  color: "var(--bs-primary1)",
                  borderRadius: "8px",
                },
                "& .MuiPaginationItem-root.Mui-selected": {
                  backgroundColor: "var(--bs-primary1)",
                  color: "#fff",
                  fontWeight: "bold",
                  "&:hover": { backgroundColor: "var(--bs-primary1)" },
                },
              }}
            />
          </Box>
        </div>
      </div>

      {/* ///Show PopUP */}
    </>
  );
};

export default AsmFundTransferReport;
